export const NAME_REGEXP: RegExp = /^[-A-Z][a-z-]{1,20}(\s[-A-Z][a-z-]{1,20})?$/;
export const EMAIL_REGEXP: RegExp = /^((([0-9A-Za-z]+[-0-9A-z.]+[0-9A-Za-z]+)|([0-9--]+[-0-9-.]+[0-9--]+))@([-A-Za-z]+\.){1,2}[-A-Za-z]{2,})$/u;
export const PHONE_REGEXP: RegExp = /^8[0-9]{10}$/;
export const CITY_REGEXP: RegExp = /^[--a-zA-Z]{2,}$/;
export const STREET_REGEXP: RegExp = /^[-A-Z-a-z\s]{2,}$/;
